<template>
    <Modal v-bind="$attrs" v-on="$listeners" :title="info && info.Path">
        <Steps :current="currentStep" size="small" :status="status">
            <Step title="Ëß£ÊûêËØ∑Ê±Ç"></Step>
            <Step title="ÂàõÂª∫ÁõÆÂΩï"></Step>
            <Step title="ÂÜôÂÖ•Êñá‰ª∂"></Step>
            <Step title="ÊâßË°ågo mod init"></Step>
            <Step title="ÊâßË°ågo build"></Step>
            <Step title="ÂêØÂä®ÂÆû‰æã"></Step>
            <Step title="ÂÆåÊàê"></Step>
        </Steps>
        <div>
            <pre>{{log}}</pre>
        </div>
        <div slot="footer">
            <Checkbox v-model="clearDir">ÂÆâË£ÖÂâçÊ∏ÖÁ©∫ÁõÆÂΩï</Checkbox>
            <Button type="primary" @click="start" :loading="status=='process'">ÂºÄÂßã</Button>
        </div>
    </Modal>
</template>

<script>
let eventSource = null;
export default {
    name: "CreateInstance",
    props: {
        info: Object
    },
    methods: {
        start() {
            this.status = "process";
            eventSource = new EventSource(
                "/instance/create?info=" +
                    JSON.stringify(this.info) +
                    (this.clearDir ? "&clear=true" : "")
            );
            eventSource.onopen = () => (this.log = "");
            eventSource.onmessage = evt => {
                this.log += evt.data + "\n";
                if (evt.data == "success") {
                    this.status = "finish";
                    eventSource.close();
                }
            };
            eventSource.addEventListener("exception", evt => {
                this.log += evt.data + "\n";
                this.status = "error";
                eventSource.close();
            });
            eventSource.addEventListener("step", evt => {
                let [step, msg] = evt.data.split(":");
                this.currentStep = step | 0;
                this.log += msg + "\n";
            });
        },
    },

    data() {
        return { clearDir: true, currentStep: 0, log: "", status: "wait" };
    }
};
</script>

